import Big from "big.js";

export default function operate(numberOne, numberTwo, operation) {
  var r ="0"
  const one = Big(numberOne || "0");
  const two = Big(numberTwo || (operation === "รท" || operation === 'x' ? "1": "0")); //If dividing or multiplying, then 1 maintains current value in cases of null
  if (operation === "+") {
    r = one.plus(two).toString();
  }
  if (operation === "-") {
    r = one.minus(two).toString();
  }
  if (operation === "x") {
    r = one.times(two).toString();
  }
  if (operation === "รท") {
    if (two == "0") {
      r = "ERROR!";
    } else {
      r = one.div(two).toString().substring(0,8);
    }
  }

  if (r.length > 8) r = "ERROR!";
  return r;
  throw Error(`Unknown operation '${operation}'`);
}
